import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  private name :String ="";
  private result :any[];  
  constructor(private http : HttpClient ) { }

  ngOnInit() {
    this.getUserData();
  }

   getUserData(){
    this.name = "bhavikmakwana"
    const response:Observable<any> = this.http.get(`https://api.github.com/users/${this.name}`)
    response.subscribe(
      (data:any[]) =>{
        this.result = data;
        console.log('Respone -> ',this.result);
        return this.result;
      }
    );
  }
}
